
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;500;700&display=swap');



/* Global Styles */
body {
  font-family: 'Roboto Mono','Arial', sans-serif;
  margin: 0;
  padding: 0;
  /*background-color: #f7f7f7;
  background-color: #0093E9;
  background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
  background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(97,186,255,1) 0%, rgba(166,239,253,1) 90.1% );
  background-color: #80d0ff;
  background-image: linear-gradient(160deg, #80d0ff 0%, #80D0C7 100%);
  background-color: #80d0ff;
  background-image: linear-gradient(160deg, #80d0ff 0%, #80D0C7 100%);*/
  background-color: #80d0ff;
  background-image: linear-gradient(160deg, #80d0ff 0%, #80D0C7 33%, #a5f9ff 66%, #ffffff 100%);

  
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  /*background-color: rgb(148, 225, 255);*/
  width: 100%;
  height: 45px;
}



.navbar .aboutbutton{
  margin-left: 20px;
  position: fixed;
  top: 15px;
  z-index: 1000;
}

.navbar .logo {
  font-size: 24px;
  font-weight: 100;
  text-decoration: none;
  color: black;
  text-align: center;
}

.navbar .buy-now {
  background-color: #4CAF50;
  padding: 10px 20px;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  margin-right: 20px;
  position: fixed;
  top: 15px;
  right: 10px;
  z-index: 1000;
}

/* Intro Section */
.intro {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  /*background-color: #E9E9E9;*/
  padding: 0px;
  position:relative;
  top: 0px;
}

.intro .introText{
  width: 30%;
  text-align: left;
}

.intro h1{
  font-size: 75px;
  text-align: left;
  margin: 0;
}

.intro p{
  font-size: 32px;
  margin-bottom: 20px;
  
}

.cyclingPics{
  height: 200px;
  width: 300px; 
  margin-left: 0%;

}

/* Product Display */
.product-display {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 100px;
  justify-content: space-evenly;
}

.product-display .product {
  width: 40%; /* considering 20px gap */
  align-items: center;
  display: flex;
  flex-direction: column;
  
}

.product-display .product img{
  width: 100%;
  height: 350px;
  border-radius: 10px;
}

.product .textOverlay {
  position:inherit;
  transform: translateY(-350px);
  left: 50%; /* Adjust as needed */
  color: white; /* or any color */
  font-size: 30px; /* Adjust as needed */
  /* Add more styling as required */
}

/* Description */
.description {
  text-align: center;
  font-size: 24pt;
  padding: 0 10% 5% 10%;
}

/* Meet Max Section */
.meet-max {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  padding: 50px 0;
  text-align: center;
}

.meet-max button{
  background-color: #4CAF50;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  padding: 1%;
  font-size: 24px;
  height: 10%;
}

.about {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: 100vh;
  padding: 0 10% 0 10%;
}

.about h1{
  text-align: center;
}

.about p{
  font-size: 26px;
}

.about img{
  width: 40%;

}

.buyNowPage{
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  gap: 5%;
  height: 100vh;
}

.buyNowPage .buySection{
  width: 30%;
  height: 90vh;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: stretch;
}

.buyNowPage .buySection img{
  width: 50%;
  margin-left: 25%;
}

/* Responsive for mobile devices */
@media (max-width: 768px) {
  .product-display {
      flex-direction: column;
  }

  .product-display .product {
      width: 100%;
      margin-bottom: 20px;
      border-radius: 5px;;
  }
}

